@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Nunito',
  sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo-text {
  font-family: 'Poppins';
  font-weight: 600;
}

.text-poppins {
  font-family: 'Poppins';
}

.navbar.logo-text,
.text-blue {
  color: #474bff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 576px) {
  .navbar.logo-text {
    font-size: .8em;
  }
}
@media screen and (max-width: 360px) {
  .navbar.logo-text {
    display: none;
  }
}
footer {
  background-color: #393939;
}

h2.footer__title {
  font-weight: 400;
}

p.footer__tagline {
  color: rgb(202, 202, 202);
}

h3.footer__subtitle {
  font-size: 1.2rem;
  font-family: 'Poppins';
}

ul {
  list-style: none;
  padding-left: 0px !important;
}

li.footer_list-item {
  color: rgb(202, 202, 202);
  margin-bottom: 5px;
}

li.footer_list-item a {
  color: rgb(202, 202, 202);
  text-decoration: none;
  transition: .2s;
}

li.footer_list-item a:hover {
  color: rgb(238, 238, 238);
}

.footer__bottom {
  min-height: 50px;
  background-color: #212124;
  color: rgb(226, 226, 226);
}
.scholarship-card {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.233);
  transition: .2s;
}

.scholarship-card:hover {
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.233);
}
.scholarship-card__title {
  font-weight: 600;
  font-size: 1em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.scholarship-card__text {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.img-card {
  height: 200px;
  width: 100%;
  object-fit: cover;
}
.verified-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14px;
  height: 14px;
  border-radius: 50%;
}

.verified-icon i {
  font-size: .7em;
}
#scholarships {
    min-height: 500px;
    background-color: #f1f0f9;
}

h1.title {
    font-size: 2rem;
}

h2.subtitle {
    font-size: 1.7rem;
}

h3.subtitle {
    font-size: 1.3rem;
}

.homepage-text{
    font-size: 2.5rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
    color: #4c3ca5;
}

.btn-action{
    background: #160F3C;
    color: #f5f5f5;
    border: none;
    min-height: 44px;
    min-width: 44px;
    border-radius: 16px;
    padding: 0 32px;
    margin-top: 5px;
    margin-left: 45.5%;
    font-weight: 600;
    transition: all .5s ease-in-out;
}

.btn-action:hover{
    transform: translateY(-8px);
    scale: 1.1;
    color: #7464d3;
}
.testi__wrapper {
    box-sizing: border-box;
}

.testi__image {
    object-fit: cover;
    max-width: 100%;
}

.testi__name {
    font-family: 'Poppins';
    font-weight: 600;
}

.testi__role {
    color: gray;
}

q.test__quote {
    quotes: "“""”""‘""’";
    line-height: 1.7em;
}

q::before {
    content: open-quote;
}

q::after {
    content: close-quote;
}
@media screen and (max-width: 576px) {
    .hero-wrapper {
        flex-direction: column-reverse;
    }
    .hero-wrapper .hero-img {
      max-width: 70%;
      height: 160px;
      object-fit: cover;
      margin-bottom: 30px;
    }
    .testi__wrapper {
        text-align: center;
    }

    .testi__image {
        max-width: 40%;
        margin-bottom: 20px;
    }
  }

.main-app-wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content-wrapper {
  flex: 1 1;
}
.bg-image{
    background-size: cover;
    height: 100vh;
}
.form-card {
    width: 90%;
}

.form-title {
    font-size: 1.4rem;
}

.form-subtitle {
    font-size: 1.2rem;
}

@media screen and (min-width: 576px) {
    .form-card {
        width: 60%;
    }
}

@media screen and (min-width: 840px) {
    .form-card {
        width: 40%;
    }
}
.bg-image{
    background-size: cover;
    height: 100vh;
}
.form-card {
    width: 95%;
}

.form-title {
    font-size: 1.4rem;
}

.form-subtitle {
    font-size: 1.2rem;
}

@media screen and (min-width: 576px) {
    .form-card {
        width: 60%;
    }
}

@media screen and (min-width: 840px) {
    .form-card {
        width: 40%;
    }
}
main h1 {
  font-size: 1.8rem;
}
.preview {
  width: 200px;
  height: 200px;
  object-fit: cover;
}
.scholarship__title {
  font-family: 'Poppins';
  font-size: 2rem;
}
.scholarship__description {
  white-space: pre-line;
}

.scholarship__breadcrumb {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 250px;
}
.poster-image {
  width: 100%;
  object-fit: cover;
}

.loading-text {
  text-align: center;
  line-height: 500px;
}
.main{
    background-color: #e9e8f0;
}

.tim{
    background-color: #dad4f8;
}

.photos{
    border: 1px solid #f5f5f5;
}

.photos-img{
    width: 72px;
    height: 72px;
    object-fit: cover;
}

.labels{
    grid-gap: 8px;
    gap: 8px;
}

.labels span{
    background: #160F3C;
    color: #f5f5f5;
    padding: 4px 12px;
    border-radius: 8px;
}

.summary{
    display: flex;
    grid-gap: 4px;
    gap: 4px;
    flex-direction: column;
}

.summary label{
    font-weight: 400;
    color: #888;
}

.summary span{
    font-weight: 500;
    font-size: 14px;
    color: #555;
}

.know-more:hover{
    transform: translateY(-8px);
    scale: 1.1;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

